// 
//  resizescrollmousemoveclick
/**
 * 4.
 * @param {} callback 
 * @param {} wait 
 */
export function throttle(callback,wait){

  // 1.
  let strat = 0;

  // 2.
  return function(e){

    // 
    let now = Date.now();
    
    // 
    if (now - strat >= wait) {

      // this
      callback.call(this,e);

      // 
      strat = now;
    }
  }
}

// 
//   input
/**
 * 5.
 * @param {} callback 
 * @param {} wait 
 */
 export function debounce(callback,wait){
  let timeId = null;
  // 
  return function(e){
    if (timeId !== null) {
      // 
      clearTimeout(timeId)
    }

    // 
    timeId = setTimeout(()=>{
      // 
      callback.call(this,e)

      // 
      timeId = null;
    },wait)
  }
}

export function format(date, fmt){
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear()+'').substr(4-RegExp.$1.length))
  }
  let o = {
    'M+': date.getMonth()+1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
  }

  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k]+'';
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str))
    }
  }
  return fmt;
}
function padLeftZero(str){
  return ('00'+str).substr(str.length);
}