import { requestAxios } from "./request";

// 
export const getHomeBannerData = async(data) => {
  return await requestAxios({
    url: '/home/multidata',
    method: 'get',
    params: data,
  })
}

// 
export const getHomeGoods = async(data) => {
  return await requestAxios({
    url: '/home/data',
    method: 'get',
    params: data,
  })
}